import type { FC } from "react";
import {
  INSTRUMENTS,
  type InstrumentParamMap,
  type InstrumentId,
} from "@/types/sequencer";

type Props = {
  params: InstrumentParamMap;
  onChange: (next: InstrumentParamMap) => void;
};

export const InstrumentControls: FC<Props> = ({ params, onChange }) => {
  const handleChange = (
    id: InstrumentId,
    field: "pitch" | "decay" | "timbre",
    value: number,
  ) => {
    const next: InstrumentParamMap = {
      ...params,
      [id]: {
        ...params[id],
        [field]: value,
      },
    };
    onChange(next);
  };

  return (
    <section className="mt-4 flex flex-col gap-2">
      <div className="font-mono text-[11px] uppercase tracking-[0.2em] text-neutral-500">
        Sound
      </div>
      <div className="grid grid-cols-1 gap-3 md:grid-cols-5">
        {INSTRUMENTS.map((id) => {
          const p = params[id];
          return (
            <div
              key={id}
              className="rounded border border-neutral-200 bg-neutral-50 px-3 py-2"
            >
              <div className="mb-1 text-[11px] capitalize text-neutral-700">
                {id}
              </div>
              <ControlRow
                label="Pitch"
                min={-24}
                max={24}
                step={1}
                value={p.pitch}
                onChange={(v) => handleChange(id, "pitch", v)}
              />
              <ControlRow
                label="Decay"
                min={0.1}
                max={1.5}
                step={0.05}
                value={p.decay}
                onChange={(v) => handleChange(id, "decay", v)}
              />
              <ControlRow
                label="Timbre"
                min={0}
                max={1}
                step={0.05}
                value={p.timbre}
                onChange={(v) => handleChange(id, "timbre", v)}
              />
            </div>
          );
        })}
      </div>
    </section>
  );
};

type ControlRowProps = {
  label: string;
  min: number;
  max: number;
  step: number;
  value: number;
  onChange: (value: number) => void;
};

const ControlRow: FC<ControlRowProps> = ({
  label,
  min,
  max,
  step,
  value,
  onChange,
}) => {
  return (
    <label className="mt-1 flex items-center gap-2 text-[11px] text-neutral-600">
      <span className="w-12 font-mono text-[10px] uppercase tracking-[0.18em]">
        {label}
      </span>
      <input
        type="range"
        min={min}
        max={max}
        step={step}
        value={value}
        onChange={(e) => onChange(Number(e.target.value))}
        className="h-1 flex-1 accent-emerald-600"
      />
      <span className="w-10 text-right font-mono text-[10px] text-neutral-700">
        {value.toFixed(2)}
      </span>
    </label>
  );
};
